<template>
    <div class="right_col" role="main">
      <div class="row">
        <div class="col-xs-12 x_title">
          <h3>动物列表</h3>
        </div>
      </div>
      <div class="row">
        <div class="col-xs-3">
          <form class="form-inline">
            <div class="form-group">
              <label for="page-size">每页显示记录数：</label>
              <select class="form-control" id="page-size" @change="selectVal($event)">
                <option :value="item" v-for="(item) in selectPage" :key="item">{{item}}</option>
              </select>
            </div>
          </form>
        </div>
        <div class="col-xs-5">
              <form class="form-inline">
              </form>
        </div> 
        <div class="col-xs-4">
          <div class="form-group has-feedback">
            <label for="product-kw" class="sr-only">搜索关键字：</label>
            <input class="form-control" type="text" id="product-kw" placeholder="请输入搜索关键字">
            <span class="glyphicon glyphicon-search form-control-feedback"></span>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-xs-12">
          <div class="table-responsive">
            <table class="table table-hover table-striped" id="table-laptop">
              <thead>
              <tr>
                <th>
                  <div class="checkbox" style="margin: 0;">
                    <label>
                      <input type="checkbox">全选
                    </label>
                  </div>
                </th>
                <th>编号</th>
                <th>动物名称</th>
                <th>动物形象</th>
                <th>动物印章</th>
                <th>操作</th>
              </tr>
              </thead>
              <tbody>
              <!-- <tr>
                <td colspan="8">
                  <div class="loading">
                    <img src="img/loading.gif" alt="">
                  </div>
                </td>
              </tr> -->
                <tr v-for="(item,index) in list" :key="item.AnimalId">
                        <td><input type="checkbox"></td>
                        <td>{{item.AnimalId}}</td>
                        <td>{{item.name}}</td>
                        <td><img :src="'http://www.zooseefun.net/data/'+item.Pic" class="pic"></td>
                        <td><img :src="item.sealPic" class="pic"></td>
                        <td>
                            <router-link :to="{path:'/updateAnimal',query:{animalId:item.AnimalId}}">修改</router-link>
                            <a href="javascript:;" @click="itemClick(item.AnimalId,item.name,item.CharacterId)">更多选项</a>
                        </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
        <div class="pages text-center">
         <mo-paging 
                  :page-index="currentPage" 
                  :total="count" 
                  :page-size="pageSize" 
                  @change="pageChange">
            </mo-paging>
        </div> 
      </div>
      <br>
      <!--模态框组件-->
     <modal title="提示" :show.sync="show"  @ok="ok"  @cancel="cancel">
            确定要删除吗？
    </modal>
    <!-- 模态框（Modal） 更多选项 -->
      <div  v-show="itemModal">
          <div class="modal fade" id="myModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel" aria-hidden="true">
            <div class="modal-dialog">
              <div class="modal-content">
                <div class="modal-header">
                  <button type="button" class="close" data-dismiss="modal" aria-hidden="true" @click="closeModal">
                    &times;
                  </button>
                  <h4 class="modal-title" id="myModalLabel">
                    更多选项
                  </h4>
                </div>
                <div class="modal-body">
                      <router-view></router-view>
                </div>
                <div class="modal-footer">
                  <!-- <button type="button" class="btn btn-default" data-dismiss="modal">关闭
                  </button> -->
                  <!-- <button type="button" class="btn btn-primary">
                    提交更改
                  </button> -->
                </div>
              </div><!-- /.modal-content -->
            </div><!-- /.modal -->
          </div>
      </div>
    </div>
</template>
<script>
import  modal from './common/modal'
import MoPaging from './common/page'
export default {
  components:{
     modal,
     MoPaging
  },  
  data(){
    return{
        itemModal:false,
        show:false,
        allZoo:'',
        productId:0,
        list:"",
        itemIndex:'',
        selectPage:[5,10,20,30],
        pageSize :5 , //每页默认显示5条数据
        currentPage : 1, //当前页码
        count : 0, //总记录数
        ZooId:window.sessionStorage.getItem('zooId'),
        itemAnimalId:''
        //items : []
    }
  },
  methods:{
      getList:function(){
             this.$http.post('/ZooCMS/animal/getAnimal',{pageNum:this.currentPage,pageSize:this.pageSize,zooId:this.ZooId},{emulateJSON: true}).then(function(response){
                        console.log(response.data);
                        this.list=response.data.data;
                        this.count=response.data.totalRow;
                        console.log(this.count);
            })
      },
      delPro:function(index,productId){
          this.itemIndex=index;
          this.productId=productId;
          this.show=true;
      },
      editEnd:function(){
        console.log(this.$refs.name,this.$refs.city)
         let updateName=this.$refs.name[this.itemIndex].innerHTML;
         this.productList[this.itemIndex].contenteditable=false;
         let city=this.$refs.city[this.itemIndex].innerHTML;
          this.$http.post('/ZooCMS/cms_zoo/updateZoo',{ZooId:this.zooId,ZooName:updateName,City:city},{emulateJSON: true}).then(function(response){
                // if(response.data=='true'){
                //         this.list.splice(this.itemIndex,1);
                //         this.show=false;
                // }
          })
      },
      updateItem:function(index,zooId){
          console.log(index);
          this.itemIndex=index;
          this.zooId=zooId;
          this.productList[index].contenteditable=true;
          //this.menu=this.list[index].Name;
          //this.$refs.edit[0].click();
          // let $item=e.target;
          // console.log($item);
      },
      ok:function(){
            // console.log(this.productId);
            // console.log("确定删除");
            // this.$http.post('/ZooCMS/productservlet',{productId:this.productId,type:'delProduct'},{emulateJSON: true}).then(function(response){
            //     if(response.data.status=='success'){
            //             this.productList.splice(this.itemIndex,1);
            //             this.show=false;
            //     }
            // })
            
      },
      cancel:function(){
            this.show=false;
            
           console.log("取消删除")
      },
      pageChange (page) {
              console.log(page);
              this.currentPage = page
              this.getList();
      },
      selectVal(e){
          console.log(e.target.value);
          this.pageSize=parseInt(e.target.value);
          this.currentPage=1;
          this.getList();
      },
      selectZooId(e){
          console.log(e.target.value);
          this.ZooId=e.target.value;
          this.getList();
      },
      itemClick(id,name,cid){
           this.itemModal=true;
           this.itemAnimalId=id;
           this.$router.push({
               path: '/list',
               query:{
                  id:id,
                  name:name,
                  cid:cid
               }
           })
           console.log(id);
      },
      closeModal(){
          this.itemModal=false;

      }
  },
  mounted:function(){
     this.getList();
     //this.getZoo();
  },
}
</script>
<style>
    .pic {
        width: 40px;
        vertical-align: middle;
    }
    .act{
        background-color:#fff;
    }
    .modal{
        display:block;
        opacity:1;
    }
    .modal-dialog{
        width:900px;
    }
    .modal.fade .modal-dialog{
        transform:translateY(30%);
    }
</style>




