<template>
         <!-- page content -->
    <div class="right_col" role="main">
      <div class="row">
        <div class="col-xs-12 x_title">
          <h3>添加新的动物园</h3>
        </div>
      </div>
      <form id="form-product" class="form-horizontal">
        <div class="form-group">
            <div class="col-sm-2">
                <label class="control-label" for="lname">动物园名称：</label>
            </div>
            <div class="col-sm-5">
                <input type="text" class="form-control"  v-model="animalName">
            </div>
            <div class="col-sm-5 help-block"></div>
        </div>
        <div class="form-group">
            <div class="col-sm-2">
                <label class="control-label" for="lname">动物园简介：</label>
            </div>
            <div class="col-sm-5">
                <textarea name="intr"  style="width:100%;" v-model="zooIntr"></textarea>
            </div>
            <div class="col-sm-5 help-block"></div>
        </div>
        <div class="form-group">
            <div class="col-sm-2">
                <label class="control-label" for="lname">上传动物园图片：</label>
            </div>
            <div class="col-sm-5">
                <uploader :src="'/api/image'" :len="3"  @filechanged="getUploadInfo"></uploader>
            </div>
            <div class="col-sm-5 help-block"></div>
        </div>
        <div class="form-group">
            <div class="col-sm-2">
                <label class="control-label" for="lname">上传封面图片：</label>
            </div>
            <div class="col-sm-5">
              <uploader :src="'/api/image'" :len="0"  @filechanged="getUpload"></uploader>
            </div>
            <div class="col-sm-5 help-block"></div>
        </div>
        <div class="form-group">
          <div class="col-sm-10 col-sm-offset-2">
            <input type="button" value="提交" class="btn btn-success" @click="formSubmit">
          </div>
        </div>

      </form>
      <div>
          <alert :show.sync="show" :alert-type="alertType" >
              {{tip}}
          </alert>
      </div>
    </div>
    <!-- /page content -->
</template>
<style>
     .imgPanel{
            width:100%;
            height:500px;
            overflow: hidden;
            position: relative;
      }
      .imgPanel img{
         position: absolute;
         top:0;
         left:0;
      }
      .imgPanel:hover{
          cursor: move;
      }
      .bgList .row{
          border-bottom:1px solid #ddd;
          padding:10px 0px;
      }
</style>

<script>
  import uploader from './common/upload'
  import alert from './common/alert'
  export default {
    components:{
        uploader,
        alert
    },
    data(){
      return{
           animalName:'',
           zooIntr:'',
           tip:"提交成功！",
           show:false,
           alertType:'',
           bgArr:'',
           map:'',
           x:0,
           y:0,
           dangerList:[
               {
                   danger:1,
                   name:'低' 
               },
               { danger:2,
                  name:'中'  
               },
               {
                 danger:3,
                 name:'高'
               }
            ],
            selectDangerId:1
      }
    },
    methods:{
        // formSubmit:function(){
        //     this.$http.post('/api/cms_zoo/setZoo',{
        //         ZooName:this.zooName,City:this.zooCity},{
        //         emulateJSON: true
        //         }).then(function(response){
        //         var data=response.data;
        //         this.show=true;
        //         if(data.status=="success"){
        //             this.tip="提交成功！"
        //         }
        //         else{
        //             this.tip=data.msg;
        //             this.alertType='alert-danger'
        //         }
        //         setTimeout(()=>{
        //                 this.show=false;
        //         },3000)
        //         })
        // },
        getMap:function(){
            this.$http.post('/ZooCMS/map/getMap',{zooId:window.sessionStorage.getItem('zooId')},{emulateJSON: true}).then(function(response){
                console.log(response);
                this.map=response.data.Path;
                this.mapId=response.data.MapId;
            })
        },
         move(e){
          let odiv = e.target;    //获取目标元素
          console.log(odiv);
            //算出鼠标相对元素的位置
            // let disX = e.clientX - odiv.offsetLeft;
            // console.log(disX);
            // let disY = e.clientY - odiv.offsetTop;
            odiv.onmousedown=(e)=>{
               this.canDrag=true;
               this.x0=e.clientX - odiv.offsetLeft;
               this.y0=e.clientY - odiv.offsetTop;
            }
            odiv.onmousemove = (e)=>{    //鼠标按下并移动的事件
              //用鼠标的位置减去鼠标相对元素的位置，得到元素的位置
              if(this.canDrag==false)return;
              let left = e.clientX - this.x0;  
              let top = e.clientY - this.x0;
              
              //绑定元素位置到positionX和positionY上面
              this.positionX = top;
              this.positionY = left;
              
              //移动当前元素
              odiv.style.left = left + 'px';
              odiv.style.top = top + 'px';
            };
            odiv.onmouseup = (e) => {
              this.canDrag=false;
              odiv.onmousemove = null;
              odiv.onmouseup = null;
            };
        }, 
        getUploadInfo:function(upLoadImg){
          this.files=upLoadImg;
          //console.log(this.files);
          // setTimeout(()=>{
          // },1000)
          console.log("开始");
        },
        getUpload:function(upLoadImg){
           this.Pic= upLoadImg;      
        },
         selectDanger:function(e){
            this.selectDangerId=e.target.value;  
         },
         changeInput:function(index){
                this.bgArr.map((v,i)=>{ 
                    if(i==index){
                         v.checked = true
                    }else{
                        v.checked = false
                    }
                })
         },
         formSubmit:function(){
          var formData = new FormData();
          formData.append('zooName',this.animalName)
          formData.append('detail',this.zooIntr)
          formDataAppend(this.Pic,'detailPath',formData);
          formDataAppend(this.files,'imgs',formData);
          this.$http.post('/ZooCMS/zoo/setZoo',formData,
            {
              headers:{
                   "Content-Type": "multipart/form-data"
              }
            },
            {emulateJSON: true}).then(function(response){         
          })
          function formDataAppend(arr,name,formData){
              console.log(arr);
              arr.forEach((item,index,array) => {
                    //console.log(item.name,item.file)
                    formData.append(name, item.file)
              })
              return formData;
          }
      }

    },
    created:function(){
    },
    mounted:function(){
      //获取商品类别
         // this.getProType();
    }
  }
</script>




