<template>
    <div>
        <h4>添加动物食物</h4>
        <form id="form-product" class="form-horizontal">
          <div class="form-group">
            <div class="col-sm-2">
                <label class="control-label" for="lname">食物名称：</label>
            </div>
            <div class="col-sm-5">
                <input type="text" class="form-control" id="lname" v-model="foodName">
            </div>
            <div class="col-sm-5 help-block"></div>
        </div>
        <!-- <div class="form-group">
          <div class="col-sm-2">
            <label class="control-label" for="title">主标题：</label>
          </div>
          <div class="col-sm-5">
            <input type="text" class="form-control" id="title" v-model="productList.name">
          </div>
          <div class="col-sm-5 help-block">商品主标题不能超过128个字符</div>
        </div> -->
        <!-- <div class="form-group">
          <div class="col-sm-2">
            <label class="control-label" for="subtitle">副标题：</label>
          </div>
          <div class="col-sm-5">
            <input type="text" class="form-control" id="subtitle">
          </div>
          <div class="col-sm-5 help-block">商品副标题不能超过128个字符</div>
        </div> -->
        <div class="form-group">
            <div class="col-sm-2">
                <label class="control-label">上传动物食物图片：</label>
            </div>
            <div class="col-sm-5">
                <uploader :src="'/api/image'" :len="0" :isActiveUpload="false" @filechanged="getUploadInfo"></uploader>
            </div>
            <div class="col-sm-5 help-block"></div>
        </div>
        <div class="form-group">
            <div class="col-sm-2">
                <label class="control-label">喜欢此食物的程度：</label>
            </div>
            <div class="col-sm-5">
                    <input type="range" max="5" min="1" step="1"  v-model="rangerVal">
            </div>
            <div class="col-sm-5 help-block"></div>
        </div>
        <div class="form-group">
          <div class="col-sm-10 col-sm-offset-2">
            <input type="button" value="提交" class="btn btn-success" @click="formSubmit">
          </div>
        </div>

      </form>
    </div>
</template>
<script>
import uploader from './common/upload'
export default {
  components:{
      uploader
  },  
  name: "food",
  data () {
    return {
        list:'',
        foodName:'',
        rangerVal:1
    };
  },
  methods:{
    getFood(){
          this.$http.post('/ZooCMS/animalinfo/getFood',{name:this.$route.query.name},{emulateJSON: true}).then(function(response){
                        console.log(response.data);
                        this.list=response.data;
            })   
    },
    getUploadInfo(img){
        this.imgInfo=img;
        console.log(this.imgInfo);
    },
    formSubmit(){
        console.log(this.rangerVal);
             var formData = new FormData();
             formData.append('food',this.foodName);
             formData.append('name',this.$route.query.name);
             formData.append('animalId',this.$route.query.id);
             formData.append('scores',this.rangerVal);
            this.common.formDataAppend(this.imgInfo,'img',formData);
            this.$http.post('/ZooCMS/animalinfo/setFood',formData,{
              headers:{
                   "Content-Type": "multipart/form-data"
              }
            },{emulateJSON: true}).then(function(response){
                        console.log(response.data);
                       // this.list=response.data;
            })     
        //  this.$http.post('/ZooCMS/animalinfo/getFood',{name:this.$route.query.name},{emulateJSON: true}).then(function(response){
        //                 console.log(response.data);
        //                 this.list=response.data;
        // })     
    }
  },
  mounted(){
      this.getFood();
  }
}
</script>
<style lang="css" scoped>
    table tr td img{
         width:40px;
    }
</style>


