        </div> -->
        <!-- <div class="form-group">
          <div class="col-sm-2">
            <label class="control-label">商品分类：</label>
          </div>
          <div class="col-sm-5">
                <select name=""  class="form-control" v-model="selected">
                      <option disabled value="0">请选择</option>
                      <option :value="item.id" v-for="(item,index) in proType" :key="item.id">
                          {{item.name}}
                      </option>
                </select>
          </div>
        </div> -->
         <!-- <div class="form-group">
          <div class="col-sm-2">
            <label class="control-label">动物性格：</label>
          </div>
          <div class="col-sm-5">
            <input type="text" class="form-control">
          </div>
          <div class="col-sm-5 help-block">数据库设计问题</div>
        </div>
        <div class="form-group">
          <div class="col-sm-2">
            <label class="control-label" for="price">单价：</label>
          </div>
          <div class="col-sm-5">
            <input type="text" class="form-control">
          </div>
          <div class="col-sm-5 help-block">商品单价最多有两位小数位</div>
        </div> -->
        <!-- <div class="form-group">
          <div class="col-sm-2">
            <label class="control-label" for="promise">服务承诺：</label>
          </div>
          <div class="col-sm-5">
            <input type="text" class="form-control" id="promise">
          </div>
          <div class="col-sm-5 help-block"></div>
        </div> -->
        <!-- <div class="form-group">
          <div class="col-sm-2">
            <label class="control-label" for="spec">规格：</label>
          </div>
          <div class="col-sm-5">
            <input type="text" class="form-control" id="spec">
          </div>
          <div class="col-sm-5 help-block"></div>
        </div> -->
         <div class="form-group">
          <div class="col-sm-2">
            <label class="control-label">上传动物馆图片：</label>
          </div>
          <div class="col-sm-5">
              <uploader :src="'/api/image'" :len="0"  @filechanged="getUploadInfo"></uploader>
          </div>
          <div class="col-sm-5 help-block"></div>
        </div>
         <div class="form-group">
          <div class="col-sm-2">
            <label class="control-label">上传详情图片：</label>
          </div>
          <div class="col-sm-5">
              <uploader :src="'/api/image'" :len='3' @filechanged="getUploadDetailInfo"></uploader>
          </div>
          <div class="col-sm-5 help-block"></div>
        </div>
        <div class="form-group">
          <div class="col-sm-2">
            <label class="control-label">详细说明：</label>
          </div>
          <div class="col-sm-10">
            <textarea name="details" class="form-control" style="width:100%;height:400px;" v-model="detail"></textarea>
          </div>
        </div>
          <div class="form-group">
          <div class="col-sm-2">
            <label class="control-label">上传声音文件：</label>
          </div>
          <div class="col-sm-5">
              <uploader :src="'/api/image'" :len='0' @filechanged="getSoundInfo"></uploader>
          </div>
          <div class="col-sm-5 help-block"></div>
        </div>
        <div class="form-group">
            <div class="col-sm-2">
                <label class="control-label">选取坐标：</label>
            </div>
            <div class="col-sm-10">
                  <div class="col-sm-12">
                      <label for="">
                            {{x+","+y}}
                      </label>
                  </div>
                  <div class="imgPanel" @mousedown="move">
                        <img src="../assets/img/beijing.jpg" alt="" @click="getPosition($event)">
                  </div>
            </div>
        </div>
        <div class="form-group">
          <div class="col-sm-10 col-sm-offset-2">
            <input type="button" value="提交" class="btn btn-success" @click="formSubmit">
          </div>
        </div>

      </form>
    </div>
    <!-- /page content -->
</template>
<style scoped>
      .imgPanel{
            width:100%;
            height:500px;
            overflow: hidden;
            position: relative;
      }
      .imgPanel img{
         position: absolute;
         top:0;
         left:0;
      }
      .imgPanel:hover{
          cursor: move;
      }
</style>

<script>
  import uploader from './common/upload'
  export default {
    components:{
        uploader
    },
    data(){
      return{
          navSubTitle:"",
          x:0,
          y:0,
          canDrag:false,
          detail:''
      }
    },
    methods:{
    //   handleLabel:function(val,index){
    //       this.handleCheck(val,index);
    //   },
    //   handleCheck:function(val,index){
    //     this.switchGroup[index].val=val==0?val=1:val=0;
    //   },
      getUploadInfo:function(upLoadImg){
          this.files=upLoadImg;
          //console.log(this.files);
          // setTimeout(()=>{
          // },1000)
          console.log("开始");
      },
      getUploadDetailInfo:function(upLoadImg){
         console.log(upLoadImg);
         this.detailFiles= upLoadImg;
      },
      getSoundInfo:function(upLoadImg){
        console.log(upLoadImg);
        this.soundFile= upLoadImg;
      },
    //   getProType:function(){
    //       this.$http.post('/apis/productservlet',{
    //         type:"getAllAnimal"},{
    //           emulateJSON: true
    //         }).then(function(response){
    //           this.proType=response.data;
    //           console.log(this.proType)
    //         })
    //   },
      move(e){
          let odiv = e.target;    //获取目标元素
          console.log(odiv);
            //算出鼠标相对元素的位置
            // let disX = e.clientX - odiv.offsetLeft;
            // console.log(disX);
            // let disY = e.clientY - odiv.offsetTop;
            odiv.onmousedown=(e)=>{
               this.canDrag=true;
               this.x0=e.clientX - odiv.offsetLeft;
               this.y0=e.clientY - odiv.offsetTop;
            }
            odiv.onmousemove = (e)=>{    //鼠标按下并移动的事件
              //用鼠标的位置减去鼠标相对元素的位置，得到元素的位置
              if(this.canDrag==false)return;
              let left = e.clientX - this.x0;  
              let top = e.clientY - this.x0;
              
              //绑定元素位置到positionX和positionY上面
              this.positionX = top;
              this.positionY = left;
              
              //移动当前元素
              odiv.style.left = left + 'px';
              odiv.style.top = top + 'px';
            };
            odiv.onmouseup = (e) => {
              this.canDrag=false;
              odiv.onmousemove = null;
              odiv.onmouseup = null;
            };
      },  
      formSubmit:function(){
        //   console.log(this.files[0].src);  
          var formData = new FormData();
          formData.append('name',this.navSubTitle)
          formData.append('zooId',this.$route.query.zooId)
          formData.append('navId',this.$route.query.navId)
          formData.append('isSound',1)
          formData.append('isDetail',1)
          formData.append('isImg',1)
          formData.append('detail',this.detail)
          formData.append('mapConnrdinate',this.x+','+this.y);
          formDataAppend(this.detailFiles,'imgs',formData);
          formDataAppend(this.soundFile,'sound',formData);
          formDataAppend(this.files,'thumb',formData);
          // formData.append('thumb',this.files)
          // formData.append('sound',this.soundFile)
          // formData.append('imgs',this.detailFiles);
          //console.log(this.files);
          // var data={
          //      'name': this.navSubTitle,
          //      'zooId':this.$route.query.zooId,
          //      'navId':this.$route.query.navId,
          //      'isSound':1,
          //      'isDetail':0,
          //      'thumb':JSON.stringify(getObjvalInArr(this.files)),
          //      'isImg':1,
          //      'imgs':JSON.stringify(getObjvalInArr(this.detailFiles))
          // }
          //console.log(data);
          this.$http.post('/api/cms_navsub/setNavSub',formData,
            {
              headers:{
                   "Content-Type": "multipart/form-data"
              }
            },
            {emulateJSON: true}).then(function(response){         
          })
          function formDataAppend(arr,name,formData){
              arr.forEach((item,index,array) => {
                    //console.log(item.name,item.file)
                    formData.append(name, item.file)
              })
              return formData;
          }
      },
      getPosition:function(event){
          console.log(event);
          this.x=event.offsetX;
          this.y=event.offsetY;
      }
    },
    created:function(){
        console.log(this.$route.query);
    },
    mounted:function(){
      //获取商品类别
        //  this.getProType();
    }
  }
</script>




