<template>
    <div>
             <router-link :to="{path:'/addFond',query:{id:id,name:name}}" class="btn btn-success">添加动物食物</router-link>
             <table class="table table-hover table-striped" id="table-laptop">
              <thead>
              <tr>
                <th>
                  <div class="checkbox" style="margin: 0;">
                    <label>
                      <input type="checkbox">全选
                    </label>
                  </div>
                </th>
                <th>编号</th>
                <th>食物名称</th>
                <th>食物图片</th>
                <th>分数</th>
                <th>操作</th>
              </tr>
              </thead>
              <tbody>
              <!-- <tr>
                <td colspan="8">
                  <div class="loading">
                    <img src="img/loading.gif" alt="">
                  </div>
                </td>
              </tr> -->
                <tr v-for="(item,index) in list" :key="index">
                    <td><input type="checkbox"></td>
                    <td>{{item.FoodId}}</td>
                    <td>{{item.Food}}</td>
                    <td><img :src="'http://www.zooseefun.net/data/'+item.Pic" alt=""></td>
                    <td>{{item.Scores}}</td>
                    <td><a href="javascript:;" @click="delItem(index,item.FoodId)">删除</a>|
                    <router-link :to="{path:'/updateFond',query:{
                        id:id,name:name,foodId:item.FoodId
                    }}">编辑</router-link></td>
                </tr>
              </tbody>
            </table> 
             <!--模态框组件-->
            <modal title="提示" :show.sync="show"  @ok="ok"  @cancel="cancel">
                    确定要删除吗？
            </modal>   
    </div>
</template>
<script>
import  modal from './common/modal'
export default {
  components:{
      modal
  },
  name: "food",
  data () {
    return {
        list:'',
        id:'',
        name:'',
        show:false
    };
  },
  methods:{
    getFood(){
          this.$http.post('api/animalinfo/getFood',{name:this.$route.query.name},{emulateJSON: true}).then(function(response){
                        console.log(response.data);
                        this.list=response.data;
            })   
    },
    delItem:function(index,id){
          this.itemIndex=index;
          this.itemId=id;
          this.show=true;
    },
     ok:function(){
            console.log(this.productId);
            console.log("确定删除");
            this.$http.post('/api/animalinfo/delFood',{foodId:this.itemId},{emulateJSON: true}).then(function(response){
                if(response.data.msg=='success'){
                        this.list.splice(this.itemIndex,1);
                        this.show=false;
                }
            })
            
      },
      cancel:function(){
            this.show=false;
            
           console.log("取消删除")
      },
  },
  mounted(){
      this.getFood();
      this.id=this.$route.query.id;
      this.name=this.$route.query.name;
  }
}
</script>
<style lang="css" scoped>
    table tr td img{
         width:40px;
    }
</style>


