<template>
    <div>
        <h4>添加成长历程</h4>
        <form id="form-product" class="form-horizontal">
                <div class="form-group" v-for="(item,index) in type" :key="index">
                    <div class="col-sm-2">
                        <label class="control-label">{{item.name}}</label>
                    </div>
                    <div class="col-sm-5">
                        <uploader :src="'/api/image'" :len="0" :isActiveUpload="false" @filechanged="getUploadInfo" :uploadkey="item.key" :imgInfo="item.path"></uploader>
                    </div>
                    <div class="col-sm-5 help-block"></div>
                </div>
                <div class="form-group">
                <div class="col-sm-10 col-sm-offset-2">
                    <input type="button" value="提交" class="btn btn-success" @click="formSubmit">
                </div>
                </div>

      </form>
    </div>
</template>
<script>
import uploader from './common/upload'
export default {
  components:{
      uploader
  },  
  name: "food",
  data () {
    return {
        list:'',
        type:[
            {
                key:'pic1',
                name:'幼年',
                path:[]
            },
            {
                key:'pic2',
                name:'中年',
                path:[]
            },
            {
                key:'pic3',
                name:'老年',
                path:[]
            }
        ],
       imgInfo:[] 
    };
  },
  methods:{
    getGrowth(){
          this.$http.post('api/animalinfo/getGrowth',{name:this.$route.query.name},{emulateJSON: true}).then(function(response){
                        console.log(response.data);
                        this.list=response.data;
                        this.list&&this.list.length!=0&&this.list.forEach((item,index)=>{
                             this.type[index].path.push({
                                 path:'http://www.zooseefun.net/data/'+item.Pic
                             })
                        })
                        console.log(this.type)
            })   
    },
    getUploadInfo(img,key){
        console.log(img)
        this.imgInfo.push({
            img:img,
            key:key
        })
    },
    formSubmit(){
             let formData = new FormData();
            //this.common.formDataAppend(this.imgInfo,'img',formData);
            formData.append('name',this.$route.query.name);
            formData.append('animalId',this.$route.query.id)
            console.log(this.imgInfo);
            this.type.forEach((item,index,arr)=>{
                    console.log(item.key);
                    try{
                        this.imgInfo.forEach((iv,i,array)=>{
                                if(index>array.length-1){
                                    console.log(index +"大于")
                                    formData.append(item.key,this.list[index].Pic);
                                    throw new Error('end');
                                }
                                if(item.key==iv.key){
                                    this.common.formDataAppend(iv.img,iv.key,formData)
                                }
                                // else{
                                //     formData.append(item.key,this.list[index].Pic)
                                // }
                        })
                    }
                    catch(e){
                         if(e.message!='end') throw e;
                    }
                   // this.imgInfo[index].key&&item.key==this.imgInfo[index].key?this.common.formDataAppend(this.imgInfo[index].img,this.imgInfo[index].key,formData):formData.append(item.key,this.list[index].Pic);
                   
            })
            //执行update方法

            if(this.list&&this.list.length!=0){
                    this.list.forEach((item,index)=>{
                         formData.append('growthId'+(index+1),item.GrowthId)
                    })
                    this.$http.post('api/animalinfo/updateGrowth',formData,{
                    headers:{
                        "Content-Type": "multipart/form-data"
                    }
                    },{emulateJSON: true}).then(function(response){
                                console.log(response.data);
                            // this.list=response.data;
                    })     
            }
            //执行inseart方法
            else{
                this.$http.post('api/animalinfo/setGrowth',formData,{
                headers:{
                    "Content-Type": "multipart/form-data"
                }
                },{emulateJSON: true}).then(function(response){
                            console.log(response.data);
                        // this.list=response.data;
                })     
            }
           
        //  this.$http.post('api/animalinfo/getFood',{name:this.$route.query.name},{emulateJSON: true}).then(function(response){
        //                 console.log(response.data);
        //                 this.list=response.data;
        // })     
    }
  },
  mounted(){
      this.getGrowth();
  }
}
</script>
<style lang="css" scoped>
    table tr td img{
         width:40px;
    }
</style>


