<template>
    <div class="right_col" role="main">
      <div class="row">
        <div class="col-xs-12 x_title">
          <h3>二级菜单</h3>
          <router-link class="btn btn-success" :to="{path:'/setSecMenu',query:{navId:this.navId,zooId:this.zooId}}">增加</router-link>
        </div>
      </div>
      <div class="row">
        <div class="col-xs-3">
          <form class="form-inline">
            <div class="form-group">
              <label for="page-size">每页显示记录数：</label>
              <select class="form-control" id="page-size" @change="selectVal($event)">
                  <option :value="item" v-for="(item) in selectPage" :key="item">{{item}}</option>
              </select>
            </div>
          </form>
        </div>
        <div class="col-xs-4 col-xs-offset-5">
          <div class="form-group has-feedback">
            <label for="product-kw" class="sr-only">搜索关键字：</label>
            <input class="form-control" type="text" id="product-kw" placeholder="请输入搜索关键字">
            <span class="glyphicon glyphicon-search form-control-feedback"></span>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-xs-12">
          <div class="table-responsive">
            <table class="table table-hover table-striped" id="table-laptop">
              <thead>
              <tr>
                <th>
                  <div class="checkbox" style="margin: 0;">
                    <label>
                      <input type="checkbox">全选
                    </label>
                  </div>
                </th>
                <th>编号</th>
                <th>动物场馆</th>
                <th>是否有详情</th>
                <th>是否有声音</th>
                <th>操作</th>
              </tr>
              </thead>
              <tbody>
              <!-- <tr>
                <td colspan="8">
                  <div class="loading">
                    <img src="img/loading.gif" alt="">
                  </div>
                </td>
              </tr> -->
                <tr v-for="(item,index) in list" :key="item.id">
                        <td><input type="checkbox"></td>
                        <td>{{item.NavSubId}}</td>
                        <td>{{item.Name}}</td>
                        <td>{{item.IsDetail==1?'是':'否'}}</td>
                        <td>{{item.IsSound==1?'是':'否'}}</td>
                        <td>
                            <router-link :to="{path:'/updateSecMenu',query:{navSubId:item.NavSubId,zooId:item.ZooId,navId:item.NavId}}" class="btn btn-warning">修改</router-link>
                            <input type="button" value="删除" class="btn btn-danger" @click="delItem(item.NavSubId,item.ZooId)">
                        </td>
                </tr>
              </tbody>
            </table>
          </div>
         <div class="pages text-center">
            <mo-paging 
                      :page-index="currentPage" 
                      :total="count" 
                      :page-size="pageSize" 
                      @change="pageChange">
                </mo-paging>
          </div> 
        </div>
      </div>
      <br>
      <!--模态框组件-->
     <!-- <modal title="提示" :show.sync="show"  @ok="ok"  @cancel="cancel">
            确定要删除吗？
    </modal> -->
    </div>
</template>
<script>
import  modal from './common/modal'
import MoPaging from './common/page'
export default {
  components:{
     modal,
     MoPaging
  },  
  data(){
    return{
        show:false,
        productId:0,
        list:"",
        itemIndex:'',
        selectPage:[5,10,20,30],
        pageSize :5 , //每页默认显示5条数据
        currentPage : 1, //当前页码
        count : 0 //总记录数
    }
  },
  methods:{
      getSecMenu:function(){
             this.$http.post('/api/cms_navsub/getNavSubList',{pageNum:this.currentPage,pageSize:this.pageSize,zooId:this.zooId,navId:this.navId},{emulateJSON: true}).then(function(response){
                    console.log(response.data);
                    this.list=response.data.NavSub;
                    this.count=response.data.totalRow;
                    //this.list=response.data.Nav;
                   //this.productList=response.data.zoo;
            })
      },
      delItem:function(NavSubId,zooId){
            this.$http.post('/api/cms_navsub/delNavSub',{navsubId:NavSubId,zooId:zooId},{emulateJSON: true}).then(function(response){
                    console.log(response.data);
                    //this.list=response.data.NavSub;
                    //this.list=response.data.Nav;
                   //this.productList=response.data.zoo;
            })
      },
      pageChange (page) {
              console.log(page);
              this.currentPage = page
              this.getSecMenu();
      },
      selectVal(e){
          this.pageSize=parseInt(e.target.value);
          this.currentPage=1
          this.getSecMenu();
      }
  },
  mounted:function(){
      this.getSecMenu();
  },
  created:function(){
      console.log(this.$route.query);
      this.navId=this.$route.query.navId;
      this.zooId=this.$route.query.zooId;
    //   this.zooId=this.$route.query.name;
    //   console.log(this.zooId);
  },
}
</script>
<style>
    #table-laptop td .pic {
        width: 40px;
        vertical-align: middle;
        position: absolute;
        margin-top: -8px;
    }
</style>




