<template>
  <div class="login">
        <div class="login_wrapper">
            <div class="animate form login_form">
            <section class="login_content">
                <form>
                <h1>zooseefun</h1>
                <div>
                    <input type="text" class="form-control" placeholder="管理员登录名" v-model="userName" required />
                </div>
                <div>
                    <input type="password" class="form-control" placeholder="管理员登录密码" v-model="userPwd  " required />
                </div>
                <div class="form-group">
                  <label for="page-size">选择动物园：</label>
                  <select class="form-control" @change="selectZooId($event)">
                    <option v-for="item in allZoo" :key="item.ZooId" :value="item.ZooId" :data-name="item.Name" ref="name">{{item.Name}}</option>
                  </select>
                </div>
                <div>
                    <a class="btn btn-default submit" @click="loginToIndex">&nbsp;登&nbsp;录&nbsp;</a>
                </div>

                <div class="clearfix"></div>

                <div class="separator">
                    <div class="clearfix"></div>
                    <br />

                    <div>
                    <h1><i>zooseefun后台管理</i></h1>
                    <p>©2018 版权所有，CODEBOY.COM</p>
                    </div>
                </div>
                </form>
            </section>
             </div>
        </div>
  </div>
</template>
<script>
export default {
    name:'login',
    data:function(){
        return {
            userName:'',
            userPwd:'',
            allZoo:'',
            ZooId:1
        }
    },
    methods:{
        selectZooId(e){
                console.log(e);
                this.ZooId=e.target.value;
                this.ZooName=this.allZoo.find(item => item.ZooId == this.ZooId)['Name'];
        },
        getZoo:function(){
            this.$http.post('/ZooCMS/zoo/getZoo',{},{emulateJSON: true}).then(function(response){
                        console.log(response.data);
                        this.allZoo=response.data;
                        let item=this.allZoo.find((item,index)=>index==0);
                        this.ZooId=item.ZooId;
                        this.ZooName=item.Name;
            })
        },
        loginToIndex:function(){
                    this.$http.post('/ZooCMS/login/login',{
                        userName:this.userName,
                        password:this.userPwd
                    },{emulateJSON: true}).then(function(response){
                                if(response.data.msg="success")
                                {
                                    console.log(response.data);
                                    this.allZoo=response.data;
                                    //let item=this.allZoo.find((item,index)=>index==0);
                                    this.$router.push('/index');
                                    window.sessionStorage.setItem('userName',this.userName);
                                    window.sessionStorage.setItem('zooId',this.ZooId);
                                    window.sessionStorage.setItem('zooName',this.ZooName);
                                }
                    })
                //console.log("点击登录");
                  
        }
    },
    mounted(){
        this.getZoo();
    }
  
}
</script>




