<template>
    <div>
        <h4>修改动物食物</h4>
        <form id="form-product" class="form-horizontal">
          <div class="form-group">
            <div class="col-sm-2">
                <label class="control-label" for="lname">食物名称：</label>
            </div>
            <div class="col-sm-5">
                <input type="text" class="form-control"  v-model="title">
            </div>
            <div class="col-sm-5 help-block"></div>
        </div>
        <!-- <div class="form-group">
          <div class="col-sm-2">
            <label class="control-label" for="title">主标题：</label>
          </div>
          <div class="col-sm-5">
            <input type="text" class="form-control" id="title" v-model="productList.name">
          </div>
          <div class="col-sm-5 help-block">商品主标题不能超过128个字符</div>
        </div> -->
        <!-- <div class="form-group">
          <div class="col-sm-2">
            <label class="control-label" for="subtitle">副标题：</label>
          </div>
          <div class="col-sm-5">
            <input type="text" class="form-control" id="subtitle">
          </div>
          <div class="col-sm-5 help-block">商品副标题不能超过128个字符</div>
        </div> -->
        <div class="form-group">
            <div class="col-sm-2">
                <label class="control-label">上传动物食物图片：</label>
            </div>
            <div class="col-sm-5">
                <uploader :src="'/api/image'" :len="0" :isActiveUpload="false" @filechanged="getUploadInfo" :imgInfo="Pic"></uploader>
            </div>
            <div class="col-sm-5 help-block"></div>
        </div>
        <div class="form-group">
             <div class="col-sm-2">
                <label class="control-label">内容：</label>
            </div>
            <div class="col-sm-5">
                 <textarea class="form-control" v-model="content"></textarea>
            </div>
            <div class="col-sm-5 help-block"></div>
        </div>
        <div class="form-group">
          <div class="col-sm-10 col-sm-offset-2">
            <input type="button" value="提交" class="btn btn-success" @click="formSubmit">
          </div>
        </div>

      </form>
    </div>
</template>
<script>
import uploader from './common/upload'
export default {
  components:{
      uploader
  },  
  name: "food",
  data () {
    return {
        list:'',
        animalname:'',
        content:'',
        Pic:[],
        title:''
    };
  },
  methods:{
    getMiracleById(){
          this.$http.post('/ZooCMS/animalinfo/getMiracleById',{miracleId:this.$route.query.MiracleId},{emulateJSON: true}).then(function(response){
                        console.log(response.data);
                       this.list=response.data;
                       this.animalname=this.list.AnimalName;
                       this.title=this.list.Title;
                       this.content=this.list.Content;
                       this.Pic.push({
                        path:'http://www.zooseefun.net/data/'+this.list.Pic
                      })  
            })   
    },
    getUploadInfo(img){
        this.imgInfo=img;
        console.log(this.imgInfo);
    },
    formSubmit(){
        console.log(this.rangerVal);
              var formData = new FormData();
             formData.append('animalId',this.$route.query.id);
             formData.append('miracleId',this.$route.query.MiracleId);
             formData.append('name',this.$route.query.name);
             formData.append('title',this.title);
             formData.append('content',this.content);
            //this.common.formDataAppend(this.imgInfo,'img',formData);
            this.imgInfo?this.common.formDataAppend(this.imgInfo,'miraclePic',formData):formData.append('miraclePic',this.list.Pic);
            this.$http.post('/ZooCMS/animalinfo/updateMiracle',formData,{
              headers:{
                   "Content-Type": "multipart/form-data"
              }
            },{emulateJSON: true}).then(function(response){
                        console.log(response.data);
                       // this.list=response.data;
            })     
        //  this.$http.post('/ZooCMS/animalinfo/getFood',{name:this.$route.query.name},{emulateJSON: true}).then(function(response){
        //                 console.log(response.data);
        //                 this.list=response.data;
        // })     
    }
  },
  mounted(){
    this.getMiracleById();
  }
}
</script>
<style lang="css" scoped>
    table tr td img{
         width:40px;
    }
</style>


