                        {"id":"","name":"添加动物信息","parentId":"1","url":"/animalAdd","icon":"","order":"1","isHeader":"0","childMenus":""}
                    ]}
                  ]
                  }  
				      ]
     }
  },
  methods:{
    handleToggle(index,item){
         console.log(item);
         if(!item.childMenus||item.childMenus.length==0){
                this.$router.push(item.url);
         }
         this.menu.forEach((items)=>{
              // for(var key in items){
              //     console.log(items);
              // }
              for(var i=0;i<items.childMenus.length;i++){
                  if(items.childMenus[i].isShow!==item.isShow){
                     items.childMenus[i].isShow=false;
                     items.childMenus[i].isActive=false;
                  }
              }
         })
         item.isShow=!item.isShow;
         item.isActive=!item.isActive;
    },
    doThis(){

    },
    navToggleStyle(){
        this.isSmMenu=!this.isSmMenu
    }
  },
  mounted(){
      
  }
}
</script>




