<template>
         <!-- page content -->
    <div class="right_col" role="main">
      <div class="row">
        <div class="col-xs-12 x_title">
          <h3>修改动物信息</h3>
        </div>
      </div>
     <form id="form-product" class="form-horizontal">
        <div class="form-group">
            <div class="col-sm-2">
                <label class="control-label" for="lname">动物名称：</label>
            </div>
            <div class="col-sm-5">
                <input type="text" class="form-control"  v-model="animalName">
            </div>
            <div class="col-sm-5 help-block"></div>
        </div>
        <div class="form-group">
            <div class="col-sm-2">
                <label class="control-label" for="lname">动物简介：</label>
            </div>
            <div class="col-sm-5">
                <textarea name="intr"  style="width:100%;" v-model="animalIntr"></textarea>
            </div>
            <div class="col-sm-5 help-block"></div>
        </div>
        <div class="form-group">
            <div class="col-sm-2">
                <label class="control-label" for="lname">上传动物形象：</label>
            </div>
            <div class="col-sm-5">
                <uploader :src="'//ZooCMS/image'" :len="0"  @filechanged="getUploadInfo" :imgInfo="thumb"></uploader>
            </div>
            <div class="col-sm-5 help-block"></div>
        </div>
         <div class="form-group">
            <div class="col-sm-2">
                <label class="control-label" for="lname">上传动物危险系数:</label>
            </div>
            <div class="col-sm-5">    
                <select class="form-control" @change="selectDanger($event)">
                    <option v-for="item in dangerList" :key="item.danger" :value="item.danger" :data-name="item.name" ref="name">{{item.name}}</option>
                </select>
            </div>
            <div class="col-sm-5 help-block"></div>
        </div>
          <div class="form-group">
            <div class="col-sm-2">
                <label class="control-label" for="lname">上传动物背景：</label>
            </div>
            <div class="col-sm-5 bgList">
                    <div class="row" v-for="(item,index) in bgArr" :key="item.BgId">
                           <div class="col-sm-3">
                                <label class="radio-inline">
                                    <input type="radio" name="optionsRadiosinline"  value="option1" :checked="item.checked" @change="changeInput(index)">背景{{item.BgId}}
                            </label>
                           </div>
                           <div class="col-sm-3">
                               <img :src="'http://www.zooseefun.net/data/'+item.PicBottom" alt="" class="img-responsive">
                           </div>
                           <div class="col-sm-3">
                               <img :src="'http://www.zooseefun.net/data/'+item.PicMiddle" alt="" class="img-responsive">
                           </div>
                           <div class="col-sm-3">
                               <img :src="'http://www.zooseefun.net/data/'+item.PicTop" alt="" class="img-responsive">
                           </div>  
                    </div>
            </div>
            <div class="col-sm-5 help-block"></div>
        </div>
        <div class="form-group">
            <div class="col-sm-2">
                <label class="control-label" for="lname">上传动物声音：</label>
            </div>
            <div class="col-sm-5">
              <uploader :src="'//ZooCMS/image'" :len="0"  @filechanged="getUploadSound" :imgInfo="sound"></uploader>
            </div>
            <div class="col-sm-5 help-block"></div>
        </div>
        <div class="form-group">
            <div class="col-sm-2">
                <label class="control-label" for="lname">上传动物印章：</label>
            </div>
            <div class="col-sm-5">
              <uploader :src="'//ZooCMS/image'" :len="0"  @filechanged="getUploadSeal" :imgInfo="seal"></uploader>
            </div>
            <div class="col-sm-5 help-block"></div>
        </div>
         <div class="form-group">
            <div class="col-sm-2">
                <label class="control-label" for="lname">动物印章：</label>
            </div>
            <div class="col-sm-10">
                    <div class="col-sm-12">
                        <label for="">
                                {{x+","+y}}
                        </label>
                    </div>
                    <div class="imgPanel" @mousedown="move">
                        <img :src="'http://www.zooseefun.net/data/'+this.map" alt="" @click="getPosition($event)" height="678px" width="980px">
                    </div>
            </div>
            <!-- <div class="col-sm-5 help-block"></div> -->
        </div>
        <div class="form-group">
          <div class="col-sm-10 col-sm-offset-2">
            <input type="button" value="提交" class="btn btn-success" @click="formSubmit">
          </div>
        </div>

      </form>
    </div>
    <!-- /page content -->
</template>
<style scoped>
      .imgPanel{
            width:100%;
            height:500px;
            overflow: hidden;
            position: relative;
      }
      .imgPanel img{
         position: absolute;
         top:0;
         left:0;
      }
      .imgPanel:hover{
          cursor: move;
      }
</style>

<script>
  import uploader from './common/upload'
  export default {
    components:{
        uploader
    },
    data(){
      return{
          animalName:"",
          animalIntr:"",
          x:0,
          y:0,
          canDrag:false,
          info:'' ,
          bgArr:[],
          sound:[],
          thumb:[],
          //详情说明
          detail:"",
          dangerList:[
               {
                   danger:1,
                   name:'低' 
               },
               { danger:2,
                  name:'中'  
               },
               {
                 danger:3,
                 name:'高'
               }
            ],
          selectDangerId:1,
          sound:[],
          seal:[],
          map:'',
          x:'',
          bgId:'',
          y:''
      }
    },
    methods:{
    //   handleLabel:function(val,index){
    //       this.handleCheck(val,index);
    //   },
    //   handleCheck:function(val,index){
    //     this.switchGroup[index].val=val==0?val=1:val=0;
    //   },
      getBg:function(id){
            this.$http.post('/ZooCMS/bg/getBg',{},{emulateJSON: true}).then(function(response){
                console.log(response);
                this.bgArr=response.data;
                this.bgArr.forEach((item,index) => {
                        console.log(item);
                        id==item.BgId?this.$set(item,'checked',true) : this.$set(item,'checked',false);
                });
                console.log(this.bgArr);
            })
      },
      selectDanger:function(e){
            this.selectDangerId=e.target.value;  
      },
      changeInput:function(index){
                this.bgArr.map((v,i)=>{ 
                    if(i==index){
                        v.checked = true
                        this.bgId=v.BgId;
                    }else{
                        v.checked = false
                    }
                })
     },
      getAnimalInfo:function(){
          this.$http.post('/ZooCMS/animal/getAnimalById',{
              animalId:this.$route.query.animalId,
              zooId:window.sessionStorage.getItem('zooId')
          },{
              emulateJSON: true
            }).then(function(response){
                console.log(response)
                this.animalInfo=response.data[0];
                this.getBg(this.animalInfo.BgId);
                this.animalName=this.animalInfo.name;
                this.animalIntr=this.animalInfo.Introduction;
                this.selectDangerId=this.animalInfo.DangerId;
                this.bgId=this.animalInfo.BgId;
                this.thumb.push({
                      path:'http://www.zooseefun.net/data/'+this.animalInfo.Pic
                })
                this.sound.push({
                    path:'http://www.zooseefun.net/data/'+this.animalInfo.soundPath
                })
                this.seal.push({
                    path:this.animalInfo.sealPath
                })
                this.x=this.animalInfo.X;
                this.y=this.animalInfo.Y;
            })
      },
      move(e){
          let odiv = e.target;    //获取目标元素
          console.log(odiv);
            //算出鼠标相对元素的位置
            // let disX = e.clientX - odiv.offsetLeft;
            // console.log(disX);
            // let disY = e.clientY - odiv.offsetTop;
            odiv.onmousedown=(e)=>{
               this.canDrag=true;
               this.x0=e.clientX - odiv.offsetLeft;
               this.y0=e.clientY - odiv.offsetTop;
            }
            odiv.onmousemove = (e)=>{    //鼠标按下并移动的事件
              //用鼠标的位置减去鼠标相对元素的位置，得到元素的位置
              if(this.canDrag==false)return;
              let left = e.clientX - this.x0;  
              let top = e.clientY - this.x0;
              
              //绑定元素位置到positionX和positionY上面
              this.positionX = top;
              this.positionY = left;
              
              //移动当前元素
              odiv.style.left = left + 'px';
              odiv.style.top = top + 'px';
            };
            odiv.onmouseup = (e) => {
              this.canDrag=false;
              odiv.onmousemove = null;
              odiv.onmouseup = null;
            };
      },  
      formSubmit:function(){
        //   console.log(this.files[0].src);  
          var formData = new FormData();
          formData.append('animalId',this.$route.query.animalId)
          formData.append('zooId',window.sessionStorage.getItem('zooId'))
          formData.append('name',this.animalName)
          formData.append('introduction',this.animalIntr)
          formData.append('sealId',this.animalInfo.SealId)
          formData.append('soundId',this.animalInfo.SoundId)
          formData.append('dangerId',this.selectDangerId)
          formData.append('bgId',this.bgId)
          formData.append('x',this.x);
          formData.append('y',this.y);
          formData.append('m/ZooCMSd',this.animalInfo.M/ZooCMSd);
          //formData.append('detail',this.detail)
          this.files?formDataAppend(this.files,'animalPic',formData):formData.append('animalPic',this.animalInfo.Pic);
          this.sealPic?formDataAppend(this.sealPic,'sealPic',formData):formData.append('sealPic',this.animalInfo.sealPath);
          this.sound?formDataAppend(this.sound,'animalSound',formData):formData.append('animalSound',this.animalInfo.soundPath);
          // formData.append('thumb',this.files)
          // formData.append('sound',this.soundFile)
          // formData.append('imgs',this.detailFiles);
          //console.log(this.files);
          // var data={
          //      'name': this.navSubTitle,
          //      'zooId':this.$route.query.zooId,
          //      'navId':this.$route.query.navId,
          //      'isSound':1,
          //      'isDetail':0,
          //      'thumb':JSON.stringify(getObjvalInArr(this.files)),
          //      'isImg':1,
          //      'imgs':JSON.stringify(getObjvalInArr(this.detailFiles))
          // }
          //console.log(data);
          this.$http.post('/ZooCMS/animal/updateAnimal',formData,
            {
              headers:{
                   "Content-Type": "multipart/form-data"
              }
            },
            {emulateJSON: true}).then(function(response){         
          })
          function formDataAppend(arr,name,formData){
            console.log(arr);
              arr.forEach((item,index,array) => {
                    //console.log(item.name,item.file)
                    if(!item.file){
                      formData.append(name,item.path);
                    }
                    else{
                      formData.append(name, item.file);
                    }
              })
              return formData;
          }
      },
      getPosition:function(event){
          console.log(event);
          this.x=event.offsetX;
          this.y=event.offsetY;
      },
      getUploadInfo:function(upLoadImg){
        this.files=upLoadImg;
        //console.log(this.files);
        // setTimeout(()=>{
        // },1000)
        console.log("开始");
      },
      getUploadSeal:function(upLoadImg){
          this.sealPic= upLoadImg;      
      },
      getUploadSound:function(upLoadImg){
          console.log(upLoadImg);
          this.sound= upLoadImg;
      },
      getMap:function(){
          this.$http.post('/ZooCMS/map/getMap',{zooId:window.sessionStorage.getItem('zooId')},{emulateJSON: true}).then(function(response){
              console.log(response);
              this.map=response.data.Path;
          })
      },
    },
    created:function(){
        console.log(this.$route.query);
    },
    mounted:function(){
      //获取商品类别
        //  this.getProType();
         this.getAnimalInfo();
         //this.getBg();
         this.getMap();
    }
  }
</script>




