<template>
         <!-- page content -->
    <div class="right_col" role="main">
      <div class="row">
        <div class="col-xs-12 x_title">
          <h3>修改动物园信息</h3>
        </div>
      </div>
     <form id="form-product" class="form-horizontal">
        <div class="form-group">
            <div class="col-sm-2">
                <label class="control-label" for="lname">动物园名称：</label>
            </div>
            <div class="col-sm-5">
                <input type="text" class="form-control"  v-model="zooName">
            </div>
            <div class="col-sm-5 help-block"></div>
        </div>
        <div class="form-group">
            <div class="col-sm-2">
                <label class="control-label" for="lname">动物园简介：</label>
            </div>
            <div class="col-sm-5">
                <textarea name="intr"  style="width:100%;" v-model="zooIntr"></textarea>
            </div>
            <div class="col-sm-5 help-block"></div>
        </div>
        <div class="form-group">
            <div class="col-sm-2">
                <label class="control-label" for="lname">上传封面图片：</label>
            </div>
            <div class="col-sm-5">
              <uploader :src="'/api/image'" :len="0"  @filechanged="getUploadInfo" :imgInfo="detail"></uploader>
            </div>
            <div class="col-sm-5 help-block"></div>
        </div>
          <div class="form-group">
            <div class="col-sm-2">
                <label class="control-label" for="lname">动物园详情图：</label>
            </div>
            <div class="col-sm-5">
              <uploader :src="'/api/image'" :len="3"  @filechanged="getUpload" :imgInfo="imgs"></uploader>
            </div>
            <div class="col-sm-5 help-block"></div>
        </div>
        <div class="form-group">
          <div class="col-sm-10 col-sm-offset-2">
            <input type="button" value="提交" class="btn btn-success" @click="formSubmit">
          </div>
        </div>

      </form>
    </div>
    <!-- /page content -->
</template>
<style scoped>
      .imgPanel{
            width:100%;
            height:500px;
            overflow: hidden;
            position: relative;
      }
      .imgPanel img{
         position: absolute;
         top:0;
         left:0;
      }
      .imgPanel:hover{
          cursor: move;
      }
</style>

<script>
  import uploader from './common/upload'
  export default {
    components:{
        uploader
    },
    data(){
      return{
          zooName:"",
          zooIntr:"",
          info:'' ,
          bgArr:[],
          detail:[],
          imgs:[],
          sound:[],
          seal:[]
      }
    },
    methods:{
    //   handleLabel:function(val,index){
    //       this.handleCheck(val,index);
    //   },
    //   handleCheck:function(val,index){
    //     this.switchGroup[index].val=val==0?val=1:val=0;
    //   },
     getZooById:function(){
          this.$http.post('/ZooCMS/zoo/getZooById',{
              zooId:this.$route.query.zooId
          },{
              emulateJSON: true
            }).then(function(response){
                console.log(response)
                this.list=response.data;
                this.zooName=this.list.ZooName;
                this.zooIntr=this.list.Detail;
                this.detail.push({
                    path:'http://www.zooseefun.net/data/'+this.list.Path
                })
                this.list.ZooIMG.forEach((item)=>{
                     this.imgs.push({
                        path:'http://www.zooseefun.net/data/'+item
                    })    
                })
               
            })
      },
      move(e){
          let odiv = e.target;    //获取目标元素
          console.log(odiv);
            //算出鼠标相对元素的位置
            // let disX = e.clientX - odiv.offsetLeft;
            // console.log(disX);
            // let disY = e.clientY - odiv.offsetTop;
            odiv.onmousedown=(e)=>{
               this.canDrag=true;
               this.x0=e.clientX - odiv.offsetLeft;
               this.y0=e.clientY - odiv.offsetTop;
            }
            odiv.onmousemove = (e)=>{    //鼠标按下并移动的事件
              //用鼠标的位置减去鼠标相对元素的位置，得到元素的位置
              if(this.canDrag==false)return;
              let left = e.clientX - this.x0;  
              let top = e.clientY - this.x0;
              
              //绑定元素位置到positionX和positionY上面
              this.positionX = top;
              this.positionY = left;
              
              //移动当前元素
              odiv.style.left = left + 'px';
              odiv.style.top = top + 'px';
            };
            odiv.onmouseup = (e) => {
              this.canDrag=false;
              odiv.onmousemove = null;
              odiv.onmouseup = null;
            };
      },  
      formSubmit:function(){
        //   console.log(this.files[0].src);  
          var formData = new FormData();
          formData.append('zooId',this.$route.query.zooId)
          formData.append('zooName',this.zooName)
          formData.append('detail',this.zooIntr)
          //formData.append('detail',this.detail)
          this.files?formDataAppend(this.files,'detailPath',formData):formData.append('detailPath',this.list.Path);
          this.sound?formDataAppend(this.Pic,'imgs',formData):formData.append('imgs',this.list.ZooIMG);
          // formData.append('thumb',this.files)
          // formData.append('sound',this.soundFile)
          // formData.append('imgs',this.detailFiles);
          //console.log(this.files);
          // var data={
          //      'name': this.navSubTitle,
          //      'zooId':this.$route.query.zooId,
          //      'navId':this.$route.query.navId,
          //      'isSound':1,
          //      'isDetail':0,
          //      'thumb':JSON.stringify(getObjvalInArr(this.files)),
          //      'isImg':1,
          //      'imgs':JSON.stringify(getObjvalInArr(this.detailFiles))
          // }
          //console.log(data);
          this.$http.post('/ZooCMS/zoo/updateZoo',formData,
            {
              headers:{
                   "Content-Type": "multipart/form-data"
              }
            },
            {emulateJSON: true}).then(function(response){         
          })
          function formDataAppend(arr,name,formData){
            console.log(arr);
              arr.forEach((item,index,array) => {
                    //console.log(item.name,item.file)
                    if(!item.file){
                      formData.append(name,item.path);
                    }
                    else{
                      formData.append(name, item.file);
                    }
              })
              return formData;
          }
      },
      getPosition:function(event){
          console.log(event);
          this.x=event.offsetX;
          this.y=event.offsetY;
      },
      getUploadInfo:function(upLoadImg){
        this.files=upLoadImg;
        //console.log(this.files);
        // setTimeout(()=>{
        // },1000)
        console.log("开始");
      },
      getUpload:function(upLoadImg){
          this.Pic= upLoadImg;      
      },
      getUploadSound:function(upLoadImg){
          console.log(upLoadImg);
          this.sound= upLoadImg;
      },
      getMap:function(){
          this.$http.post('/api/map/getMap',{zooId:window.sessionStorage.getItem('zooId')},{emulateJSON: true}).then(function(response){
              console.log(response);
              this.map=response.data.Path;
          })
      },
    },
    created:function(){
        console.log(this.$route.query);
    },
    mounted:function(){
        //
        this.getZooById()
    }
  }
</script>




