// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import Common from './common'
import "./assets/css/bootstrap.css"
import "./assets/css/font-awesome.css"
import "./assets/css/app.css"
import Router from 'vue-router'
import VueResource from 'vue-resource'
import Login from './components/login'
import Index from './components/index'
import myChart from './components/myChart'
import productList from './components/productList'
import setFirstMenu from './components/setFirstMenu'
import setSecMenu from './components/setSecMenu'
import animalAdd from './components/animalAdd'
import getFirstMenu from './components/getFirstMenu' 
import getSecMenu from './components/getSecMenu'
import updateAnimal from './components/updateAnimal'
import list from './components/list'
import food from './components/food'
import addFond from './components/addFond'
import updateFond from './components/updateFond'
import home from './components/home'
import addHome from './components/addhome'
import updateHome from './components/updateHome'
import character from './components/character'
import addCharacter from './components/addCharacter'
import updateCharacter from './components/updateCharacter'
import grow from './components/grow'
import Miracle from './components/Miracle'
import addMiracle from './components/addMiracle'
import updateMiracle from './components/updateMiracle'

import addZoo from './components/addZoo'
import zoolist from './components/zoolist'
import updateZoo from './components/updateZoo'
import loading from './components/common/loading'
Vue.config.productionTip = false
Vue.prototype.common=Common
Vue.use(Router)
Vue.use(VueResource)
/* eslint-disable no-new */
let router=new Router({
  routes: [
    {
      path: '/',
      component:Login
    },
    {
      path:'/login',
      component:Login
    },
    {
     path:'/index',
     component:Index,
     children:[
        {
          path:'',
          component:myChart
        },
        {
          path:'/productList',
          component:productList,
          children:[
              {
                path:'/list',
                component:list
              },
              {
                path:'/food',
                component:food
              },
              {
                path:'/addFond',
                component:addFond
              },
              {
                path:'/updateFond',
                component:updateFond
              },
              {
                path:'/home',
                component:home
              },
              {
                path:'/addHome',
                component:addHome
              },
              {
                path:'/updateHome',
                component:updateHome
              },
              {
                path:'/character',
                component:character
              },
              {
                path:'/addCharacter',
                component:addCharacter
              },
              {
                path:'/updateCharacter',
                component:updateCharacter
              },
              {
                path:'/grow',
                component:grow
              },
              {
                path:'/Miracle',
                component:Miracle
              },
              {
                path:'/addMiracle',
                component:addMiracle
              },
              {
                path:'/updateMiracle',
                component:updateMiracle
              }
          ]
        },
        {
          path:'/setFirstMenu',
          component:setFirstMenu
        },
        {
          path:'/animalAdd',
          component:animalAdd
        },
        {
          path:'/getFirstMenu',
          component:getFirstMenu
        },
        {
          path:'/getSecMenu',
          component:getSecMenu
        },
        {
          path:'/setSecMenu',
          component:setSecMenu
        },
        {
          path:'/updateAnimal',
          component:updateAnimal
        },
        {
          path:'/addZoo',
          component:addZoo
        },
        {
          path:'/zoolist',
          component:zoolist
        },
        {
          path:'/updateZoo',
          component:updateZoo
        }
      ]
    }
  ]
})
var app=new Vue({
  el: '#app',
  components: { App},
  template: '<App/>',
  router
})




