<template>
    <div>

            <router-link :to="{path:'/addCharacter',query:{id:id,name:name}}" class="btn btn-success" v-if="desc">添加动物性格</router-link>
             <div class="form-group" v-if="!desc">
                <div class="col-sm-5">
                    <input type="text" class="form-control" id="lastname" placeholder="请输入动物性格描述" v-model="newDesc">
                </div>
                <div class="col-sm-3">
                    <button class="btn btn-success" @click="addDesc()">增加</button>
                </div>
            </div>
             <div v-else>
                 {{desc}}
             </div>
             
             <table class="table table-hover table-striped" id="table-laptop">
              <thead>
              <tr>
                <th>
                  <div class="checkbox" style="margin: 0;">
                    <label>
                      <input type="checkbox">全选
                    </label>
                  </div>
                </th>
                <th>编号</th>
                <th>性格</th>
                <th>分数</th>
                <th>操作</th>
              </tr>
              </thead>
              <tbody>
              <!-- <tr>
                <td colspan="8">
                  <div class="loading">
                    <img src="img/loading.gif" alt="">
                  </div>
                </td>
              </tr> -->
                <tr v-for="(item,index) in list" :key="index">
                    <td><input type="checkbox"></td>
                    <td>{{item.classifyId}}</td>
                    <td>{{item.classify}}</td>
                    <td>{{item.scores}}</td>
                    <td><a href="javascript:;" @click="delItem(index,item.item.classifyId)">删除</a>|
                    <router-link :to="{path:'/updateCharacter',query:{
                        id:id,name:name,classifyId:item.classifyId
                    }}">编辑</router-link></td>
                </tr>
              </tbody>
            </table> 
             <!--模态框组件-->
            <modal title="提示" :show.sync="show"  @ok="ok"  @cancel="cancel">
                    确定要删除吗？
            </modal>   
    </div>
</template>
<script>
import  modal from './common/modal'
export default {
  components:{
      modal
  },
  name: "food",
  data () {
    return {
        list:'',
        id:'',
        name:'',
        show:false,
        newDesc:'',
        desc:''
    };
  },
  methods:{
    getCharacter(cid,name){
          this.$http.post('/ZooCMS/animalinfo/getCharacter',{name:name,characterId:cid},{emulateJSON: true}).then(function(response){
                        console.log(response.data);
                        this.list=response.data.data;
                        this.desc=response.data.Description;
            })   
    },
    addDesc(){
        this.$http.post('/ZooCMS/animalinfo/setDescription',{animalId:this.id,description:this.newDesc},{emulateJSON: true}).then(function(response){
                        console.log(response.data);
                        if(response.data.id){
                            getCharacter(response.data.id,this.name);
                        }
                        
        })   
    },
    delItem:function(index,id){
          this.itemIndex=index;
          this.itemId=id;
          this.show=true;
    },
     ok:function(){
            console.log(this.productId);
            console.log("确定删除");
            this.$http.post('//ZooCMS/animalinfo/delFood',{foodId:this.itemId},{emulateJSON: true}).then(function(response){
                if(response.data.msg=='success'){
                        this.list.splice(this.itemIndex,1);
                        this.show=false;
                }
            })
            
      },
      cancel:function(){
            this.show=false;
            
           console.log("取消删除")
      },
  },
  mounted(){
      this.id=this.$route.query.id;
      this.name=this.$route.query.name;
      this.cid=this.$route.query.cid;
      if(!this.cid){
          return false;
      }
      this.getCharacter(this.cid,this.name);
  }
}
</script>
<style lang="css" scoped>
    table tr td img{
         width:40px;
    }
</style>


