<template>
    <div>
        <h4>添加动物性格</h4>
        <form id="form-product" class="form-horizontal">
          <div class="form-group">
            <div class="col-sm-2">
                <label class="control-label" for="lname">性格名称：</label>
            </div>
            <div class="col-sm-5">
                <input type="text" class="form-control" id="lname" v-model="classify">
            </div>
            <div class="col-sm-5 help-block"></div>
        </div>
        <div class="form-group">
            <div class="col-sm-2">
                <label class="control-label">性格程度：</label>
            </div>
            <div class="col-sm-5">
                    <input type="range" max="5" min="1" step="1"  v-model="rangerVal">
            </div>
            <div class="col-sm-5 help-block"></div>
        </div>
        <div class="form-group">
          <div class="col-sm-10 col-sm-offset-2">
            <input type="button" value="提交" class="btn btn-success" @click="formSubmit">
          </div>
        </div>

      </form>
    </div>
</template>
<script>
import uploader from './common/upload'
export default {
  components:{
      uploader
  },  
  name: "food",
  data () {
    return {
        list:'',
        classify:'',
        rangerVal:1
    };
  },
  methods:{
    getFood(){
          this.$http.post('/ZooCMS/animalinfo/getFood',{name:this.$route.query.name},{emulateJSON: true}).then(function(response){
                        console.log(response.data);
                        this.list=response.data;
            })   
    },
    getUploadInfo(img){
        this.imgInfo=img;
        console.log(this.imgInfo);
    },
    formSubmit(){
         this.$http.post('/ZooCMS/animalinfo/setCharacter',{name:this.$route.query.name,classify:this.classify,scores:this.rangerVal},{emulateJSON: true}).then(function(response){
                        console.log(response.data);
        })     
    }
  },
  mounted(){
      this.getFood();
  }
}
</script>
<style lang="css" scoped>
    table tr td img{
         width:40px;
    }
</style>


