<template>
    <div class="right_col" role="main">
      <div class="row">
        <div class="col-xs-12 x_title">
          <h3>一级菜单</h3>
        </div>
      </div>
      <div class="row">
        <div class="col-xs-3">
          <form class="form-inline">
            <div class="form-group">
              <label for="page-size">每页显示记录数：</label>
              <select class="form-control" id="page-size" @change="selectVal($event)">
                <option :value="item" v-for="item in selectPage" :key="item">{{item}}</option>
              </select>
            </div>
          </form>
        </div>
        <div class="col-xs-4 col-xs-offset-5">
          <div class="form-group has-feedback">
            <label for="product-kw" class="sr-only">搜索关键字：</label>
            <input class="form-control" type="text" id="product-kw" placeholder="请输入搜索关键字">
            <span class="glyphicon glyphicon-search form-control-feedback"></span>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-xs-12">
          <div class="table-responsive">
            <table class="table table-hover table-striped" id="table-laptop">
              <thead>
              <tr>
                <th>
                  <div class="checkbox" style="margin: 0;">
                    <label>
                      <input type="checkbox">全选
                    </label>
                  </div>
                </th>
                <th>编号</th>
                <th>一级菜单</th>
                <th>操作</th>
              </tr>
              </thead>
              <tbody>
              <!-- <tr>
                <td colspan="8">
                  <div class="loading">
                    <img src="img/loading.gif" alt="">
                  </div>
                </td>
              </tr> -->
                <tr v-for="(item,index) in list" :key="item.id">
                        <td><input type="checkbox"></td>
                        <td>{{item.NavId}}</td>
                        <td :contenteditable="item.contenteditable" class="itemName" ref="edit" @blur="editEnd">{{item.Name}}</td>
                        <td>
                            <router-link :to="{path:'/setSecMenu',query:{navId:item.NavId,zooId:item.ZooId}}">设置二级菜单</router-link>
                            <router-link :to="{path:'/getSecMenu',query:{navId:item.NavId,zooId:item.ZooId}}">查看二级菜单</router-link>
                            <a href="javascript:;" @click="updateItem(index,item.ZooId,item.NavId)">修改</a>
                            <a href="javascript:;" @click="delItem(index,item.ZooId,item.NavId)">删除</a>
                        </td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="pages text-center">
            <mo-paging 
                      :page-index="currentPage" 
                      :total="count" 
                      :page-size="pageSize" 
                      @change="pageChange">
                </mo-paging>
          </div> 
        </div>
      </div>
      <br>
      <!--模态框组件-->
     <modal title="提示" :show.sync="show"  @ok="ok"  @cancel="cancel">
            确定要删除吗？
    </modal>
    </div>
</template>
<script>
import  modal from './common/modal'
import MoPaging from './common/page'
export default {
  components:{
     modal,
     MoPaging
  },  
  data(){
    return{
        show:false,
        productId:0,
        list:"",
        itemIndex:'',
        selectPage:[5,10,20,30],
        pageSize :5 , //每页默认显示5条数据
        currentPage : 1, //当前页码
        count : 0 //总记录数
    }
  },
  methods:{
      getSecMenu:function(){
             this.$http.post('/api/cms_nav/getNavList',{pageNum:1,pageSize:10,zooId:this.zooId},{emulateJSON: true}).then(function(response){
                    console.log(response.data);
                    this.list=response.data.Nav;
                   //this.productList=response.data.zoo;
                    this.list.forEach(item => {
                          console.log(item);
                          this.$set(item,'contenteditable',false);
                    });
                    console.log(this.list);
            })
      },
      editEnd:function(){
         let updateName=this.$refs.edit[this.itemIndex].innerHTML
          this.$http.post('/api/cms_nav/updateNav',{zooId:this.zooId,navId:this.navId,name:updateName},{emulateJSON: true}).then(function(response){
                // if(response.data=='true'){
                //         this.list.splice(this.itemIndex,1);
                //         this.show=false;
                // }
          })
      },
      delItem:function(index,zooId,navId){
          this.itemIndex=index;
          this.zooId=zooId;
          this.navId=navId;
          this.show=true;
      },
      ok:function(){
            //console.log(this.productId);
            console.log("确定删除");
            this.$http.post('/api/cms_nav/delNav',{zooId:this.zooId,navId:this.navId},{emulateJSON: true}).then(function(response){
                if(response.data=='true'){
                        this.list.splice(this.itemIndex,1);
                        this.show=false;
                }
            })
            
      },
      cancel:function(){
            this.show=false;
           console.log("取消删除");
      },
      updateItem:function(index,zooId,navId){
          console.log(index);
          this.zooId=zooId;
          this.navId=navId;
          this.itemIndex=index;
          this.list[index].contenteditable=true;
          //this.menu=this.list[index].Name;
          console.log(this.$refs.edit);
          //this.$refs.edit[0].click();
          // let $item=e.target;
          // console.log($item);
      },
      pageChange (page) {
              console.log(page);
              this.currentPage = page
              this.getProduct();
      },
      selectVal(e){
          this.pageSize=parseInt(e.target.value);
          this.currentPage=1
          this.getSecMenu();
      }
  },
  mounted:function(){
      //this.getProduct();
      this.getSecMenu();
  },
  created:function(){
      this.zooId=this.$route.query.name;
      console.log(this.zooId);
  },
}
</script>
<style>
    #table-laptop td .pic {
        width: 40px;
        vertical-align: middle;
        position: absolute;
        margin-top: -8px;
    }
</style>




