<template>
        <div class="alert-content" v-show="show">  
            <div :class="['alert',alertType,'alert-dismissable']">
                <!-- <button type="button" class="close" data-dismiss="alert"
                        aria-hidden="true">
                    &times;
                </button> -->
                <slot>{{tip}}</slot>
            </div>
        </div>  
</template>
<script>
export default {
  name: "alert",
  props:{
      show: {
            type: Boolean,
            default: false
      },
      tip: {
            type: String,
            default: '成功！很好地完成了提交。'
      },
      alertType:{
          type:String,
          default:'alert-success' // alert-success,alert-info,alert-warning,alert-danger 
      }
  },
  data () {
    return {
    };
  },
}
</script>
<style lang="css" scoped>
</style>


