<template>
    <div>
        <h4>添加"住哪"</h4>
        <form id="form-product" class="form-horizontal">
          <div class="form-group">
            <div class="col-sm-2">
                <label class="control-label" for="lname">国家名称：</label>
            </div>
            <div class="col-sm-5">
                <input type="text" class="form-control"  v-model="CountryName">
            </div>
            <div class="col-sm-5 help-block"></div>
        </div>
        <!-- <div class="form-group">
            <div class="col-sm-2">
                <label class="control-label">经纬度：</label>
            </div>
            <div class="col-sm-5">
                    <input type="text" class="form-control" readonly v-model="point">
            </div>
            <div class="col-sm-5 help-block"></div>
        </div> -->
        <!-- <div class="form-group">
              <div id="map" ref="map"></div>
        </div> -->
        <div class="form-group">
          <div class="col-sm-10 col-sm-offset-2">
            <input type="button" value="提交" class="btn btn-success" @click="formSubmit">
          </div>
        </div>

      </form>
    </div>
</template>
<script>
import uploader from './common/upload'
export default {
  components:{
      uploader
  },  
  name: "food",
  data () {
    return {
        list:'',
        CountryName:'',
        point:''
    };
  },
  methods:{
    getFood(){
          this.$http.post('/ZooCMS/animalinfo/getFood',{name:this.$route.query.name},{emulateJSON: true}).then(function(response){
                        console.log(response.data);
                        this.list=response.data;
            })   
    },
    getUploadInfo(img){
        this.imgInfo=img;
        console.log(this.imgInfo);
    },
    formSubmit(){
        console.log(this.rangerVal);
            this.$http.post('/ZooCMS/animalinfo/setHome',{country:this.CountryName,name:this.name},{emulateJSON: true}).then(function(response){
                        console.log(response.data);
                       // this.list=response.data;
            })     
        //  this.$http.post('/ZooCMS/animalinfo/getFood',{name:this.$route.query.name},{emulateJSON: true}).then(function(response){
        //                 console.log(response.data);
        //                 this.list=response.data;
        // })     
    },
    getMap(){
            this.map = new BMap.Map(this.$refs.map);
            this.map.centerAndZoom(new BMap.Point(116.404, 39.915),1);// 初始化地图,设置中心点坐标和地图级别
            this.map.addControl(new BMap.MapTypeControl({//添加地图类型控件
            mapTypes:[  
                BMAP_NORMAL_MAP,
                BMAP_HYBRID_MAP
                ]}));
            this.map.setCurrentCity("北京");// 设置地图显示的城市 此项是必须设置的
            this.map.enableScrollWheelZoom(true); //开启鼠标滚轮缩放
            this.localSearch = new BMap.s(this.map);
            this.localSearch.enableAutoViewport(); //允许自动调节窗体大小
    },
    searchByStationName() {
            this.map.clearOverlays();//清空原来的标注
        　　let keyword = this.CountryName;
        　　this.localSearch.setSearchCompleteCallback((searchResult)=> {
        　　　　let poi = searchResult.getPoi(0);
               console.log(poi);
        　　　　this.point = poi.point.lng + "," + poi.point.lat; //获取经度和纬度，将结果显示在文本框中
        　　　　this.map.centerAndZoom(poi.point,4);
                let marker = new BMap.Marker(new BMap.Point(poi.point.lng, poi.point.lat));  // 创建标注，为要查询的地方对应的经纬度
                this.map.addOverlay(marker);
        　　});
        　　this.localSearch.search(keyword);
    }
  },
  mounted(){
      this.getFood();
      //this.id=this.$route.query.id;
      this.name=this.$route.query.name;
      //this.getMap();
  }
}
</script>
<style lang="css" scoped>
    table tr td img{
         width:40px;
    }
    #map{
        width:100%;
        height:300px;
    }
</style>


