<template>
      <div>
            <ul>
                  <li v-for="(item,index) in itemArr" :key="index">
                    <router-link :to="{path:item.url,query:{id:id,name:name,cid:cid}}">{{item.name}}</router-link>
                        <!-- {{item.name}} -->
                  </li>
            </ul>
      </div>
</template>
<script>
  export default {
    data(){
      return{
         //更多选项
        id:'',
        cid:'',
        name:'',
        itemArr:[{
                  url:"/food",
                  name:'吃'
                },
                {
                  url:'/home',
                  name:'住哪'
                },
                {
                  url:'/character',
                  name:'性格'
                },
                {
                  url:'/Miracle',
                  name:'奇葩点'
                },
                {
                  url:'/grow',
                  name:'成长历程'
                }
          ]
      }
    },
    methods:{
    },
    created:function(){
    },
    mounted:function(){
      //获取商品类别
      this.id=this.$route.query.id;
      this.name=this.$route.query.name;
      this.cid=this.$route.query.cid;
         // this.getProType();
    }
  }
</script>




