<template>
         <!-- page content -->
    <div class="right_col" role="main">
      <div class="row">
        <div class="col-xs-12 x_title">
          <h3>增加一级菜单</h3>
        </div>
      </div>
      <form id="form-product" class="form-horizontal">
        <div class="form-group">
          <div class="col-sm-2">
            <label class="control-label" for="title">一级标题：</label>
          </div>
          <div class="col-sm-5">
            <input type="text" class="form-control" id="title" v-model="zooNav">
          </div>
          <div class="col-sm-5 help-block"></div>
        </div>
        <!-- <div class="form-group">
          <div class="col-sm-2">
            <label class="control-label">{{item.label}}</label>
          </div>
          <div class="col-sm-5">
            <div class="checkbox">
                  <input type="checkbox" class="widget_switch_checkbox" :checked='item.val' @click="handleCheck(item.val,index)">
                   <label for="widget_switch_checkbox" class="widget_switch_label" @click="handleLabel(item.val,index)">
                      <span class="widget_switch_circle"></span>
                  </label>
            </div>
          </div>
          <div class="col-sm-5 help-block"></div>
        </div> -->
        <div class="form-group">
          <div class="col-sm-10 col-sm-offset-2">
            <input type="button" value="增加" class="btn btn-success" @click="submitById">
          </div>
        </div>

      </form>
    </div>
    <!-- /page content -->
</template>
<script>
import uploader from './common/upload'
export default {
  components:{
     uploader
  },
  data(){
    return{
        zooNav:""
    }
  },
  methods:{
      submitById:function(){
           this.$http.post('/api/cms_nav/setNav',{zooId:this.zooId,Name:this.zooNav},{emulateJSON: true}).then(function(response){
                    this.productList=response.data;
                    // this.getProType();
                    // this.selected=this.productList.animalId
            })
      }
  },
  created:function(){
      this.zooId=this.$route.query.name;
      console.log(this.zooId);
  },
  mounted:function(){
      //this.getProductById();
  },
}
</script>




