<template>
    <div>
        <h4>修改动物性格</h4>
        <form id="form-product" class="form-horizontal">
          <div class="form-group">
            <div class="col-sm-2">
                <label class="control-label" for="lname">性格名称：</label>
            </div>
            <div class="col-sm-5">
                <input type="text" class="form-control" id="lname" v-model="classify">
            </div>
            <div class="col-sm-5 help-block"></div>
        </div>
        <!-- <div class="form-group">
          <div class="col-sm-2">
            <label class="control-label" for="title">主标题：</label>
          </div>
          <div class="col-sm-5">
            <input type="text" class="form-control" id="title" v-model="productList.name">
          </div>
          <div class="col-sm-5 help-block">商品主标题不能超过128个字符</div>
        </div> -->
        <!-- <div class="form-group">
          <div class="col-sm-2">
            <label class="control-label" for="subtitle">副标题：</label>
          </div>
          <div class="col-sm-5">
            <input type="text" class="form-control" id="subtitle">
          </div>
          <div class="col-sm-5 help-block">商品副标题不能超过128个字符</div>
        </div> -->
        <div class="form-group">
            <div class="col-sm-2">
                <label class="control-label">性格程度：</label>
            </div>
            <div class="col-sm-5">
                    <input type="range" max="5" min="1" step="1"  v-model="rangerVal">
            </div>
            <div class="col-sm-5 help-block"></div>
        </div>
        <div class="form-group">
          <div class="col-sm-10 col-sm-offset-2">
            <input type="button" value="提交" class="btn btn-success" @click="formSubmit">
          </div>
        </div>

      </form>
    </div>
</template>
<script>
import uploader from './common/upload'
export default {
  components:{
      uploader
  },  
  name: "food",
  data () {
    return {
        list:'',
        classify:'',
        rangerVal:1,
    };
  },
  methods:{
    getCharacterById(){
          this.$http.post('api/animalinfo/getCharacterById',{classifyId:this.$route.query.classifyId},{emulateJSON: true}).then(function(response){
                      console.log(response.data);
                      this.data=response.data
                      this.classify=this.data.Classify;
                      this.rangerVal=this.data.Scores;

            })   
    },
    getUploadInfo(img){
        this.imgInfo=img;
        console.log(this.imgInfo);
    },
    formSubmit(){
        console.log(this.rangerVal);

         this.$http.post('api/animalinfo/updateCharacter',{name:this.$route.query.name,classifyId:this.$route.query.classifyId,classify:this.classify,scores:this.rangerVal},{emulateJSON: true}).then(function(response){
                        console.log(response.data);
                        this.list=response.data;
        })     
    }
  },
  mounted(){
    this.getCharacterById();
  }
}
</script>
<style lang="css" scoped>
    table tr td img{
         width:40px;
    }
</style>


